import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage'
import MLSearch from '@/components/MLSearch'
import RoughSort from '@/components/RoughSort'
import GRClickShow from '@/components/GRClickShow'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {path: '/', name: 'HomePage', component: HomePage, meta: {title: 'Trace'}},
    {path: '/mlSearch', name: 'MLSearch', component: MLSearch, meta: {title: 'Trace-'}},
    {path: '/roughsort', name: 'RoughSort', component: RoughSort, meta: {title: 'Trace-'}},
    {path: '/grClickShow', name: 'GRClickShow', component: GRClickShow, meta: {title: 'Trace-'}}
  ]
})



